import React, {
  cloneElement,
  memo,
  RefObject,
  useCallback,
  useMemo,
  useRef,
  useState,
} from 'react';
import {
  AnimatedKeyboardAvoidingViewRef,
  BottomButton,
  MyKeyboardAvoidingView,
  UIInput,
  UISelectorInput,
} from '@true/ui';
import {
  Fonts,
  getUserGroup,
  goBack,
  IC_ARROW,
  styled,
  translate,
  useTheme,
  useUserGroupByQuery,
} from '@true/core';
import {
  getCollections,
  useCollections,
  useCollectionsByQuery,
} from '@policy/store/collections';
import {getStorages, useStoragesByQuery} from '@policy/store/storages';
import {getDoctypes, useDoctypesByQuery} from '@policy/store/doctypes';
import {AnimatedBottomSpace} from '../AnimatedBottomSpace';
import {ScrollView, View} from 'react-native';
import Animated, {
  interpolate,
  runOnJS,
  SharedValue,
  useAnimatedReaction,
  useAnimatedStyle,
  useSharedValue,
  withTiming,
} from 'react-native-reanimated';

const filterOpts = [
  {label: translate('policy.bookmarked'), value: 'bookmarked'},
  {label: translate('policy.created'), value: 'created'},
  {label: translate('policy.reviewed'), value: 'reviewed'},
  {label: translate('policy.published'), value: 'published'},
  {label: translate('policy.pending'), value: 'pending'},
  {label: translate('policy.rejected'), value: 'rejected'},
];
const sortOpts = [
  {label: translate('policy.issued'), value: 'issued'},
  {label: translate('policy.effective'), value: 'effective'},
];

type _TInputFilter =
  | 'collection'
  | 'storage'
  | 'receiver'
  | 'doctype'
  | 'filter'
  | 'sort'
  | 'created_by'
  | 'folder'
  | 'created_date'
  | 'effective_date'
  | 'expired_date'
  | ((params: any) => JSX.Element);

export type IInputFilterType = _TInputFilter | _TInputFilter[];

interface Props {
  items: IInputFilterType[];
  params: any;
  setParams: (params: any) => any;
  defaultParams: any;
}

export const FilterComp = memo((props: Props) => {
  const {items, params, setParams, defaultParams} = props;

  const ref = useRef<{[id: string]: UISelectorInput}>({});

  const _params = useSharedValue(params);

  const setAdvancedExport = useCallback(() => {
    for (let item of items) {
      /* if item is advanced*/
      if (Array.isArray(item)) {
        for (let _type of item) {
          /* if type is JSX.Element, return keyName



          * else return default type */
          const keyName =
            typeof _type !== 'string'
              ? cloneElement(_type({})).props?.keyName
              : _type.indexOf('date') !== -1
              ? [_type + '_from', _type + '_to']
              : _type;

          /* if there keyName in params add advanced_export */
          if (
            Array.isArray(keyName)
              ? _params.value[keyName[0]] || _params.value[keyName[1]]
              : _params.value[keyName]
          ) {
            _params.value = {
              ..._params.value,
              advanced_export: 1,
            };
          }
        }
      }
    }
  }, []);

  useAnimatedReaction(
    () => _params.value,
    (__params, __prevParams) => {
      if (
        !!__prevParams?.collection &&
        __params.collection !== __prevParams.collection
      ) {
        _params.value = {
          ..._params.value,
          folder: undefined,
        };
      }

      if (JSON.stringify(__params) !== JSON.stringify(__prevParams)) {
        runOnJS(setAdvancedExport)();
      }
    },
    [],
  );

  const _onSubmit = useCallback(() => {
    setParams(_params.value);
    goBack();
  }, [_params, setParams]);

  const onReset = useCallback(() => {
    setParams(defaultParams);
    goBack();
  }, [setParams]);

  const theme = useTheme();

  const keyboardRef = useRef<AnimatedKeyboardAvoidingViewRef>(null);
  const scrollRef = useRef<ScrollView>(null);

  return (
    <SMyKeyboardAvoidingView
      disableOnAndroid={true}
      scrollInputAdditionalOffset={110}
      scrollRef={scrollRef}
      ref={keyboardRef}>
      <ScrollView ref={scrollRef}>
        <ListItems itemsRef={ref} params={_params} items={items} />
        <View style={{height: 16}} />
      </ScrollView>
      <View style={{height: 1, backgroundColor: theme.neutral5}} />
      <BottomButton
        containerStyle={{
          marginHorizontal: 16,
          marginVertical: 12,
          borderRadius: 20,
        }}
        leftContainerStyle={{
          borderWidth: 1,
          borderColor: theme.primaryColor,
          borderRadius: 8,
        }}
        rightContainerStyle={{
          borderRadius: 8,
        }}
        leftBgColor={theme.white}
        textColor={theme.primaryColor}
        leftText={translate('policy.reset')}
        rightText={translate('policy.apply')}
        onRightPress={_onSubmit}
        onLeftPress={onReset}
      />
      <AnimatedBottomSpace />
    </SMyKeyboardAvoidingView>
  );
});

const ListItems = memo(
  ({
    items,
    params,
    itemsRef,
  }: {
    items: IInputFilterType[];
    params: SharedValue<any>;
    itemsRef: RefObject<{[id: string]: UISelectorInput | null}>;
  }) => {
    return (
      <>
        {items.map(item => (
          <SItemContainer>
            <ItemInput itemsRef={itemsRef} type={item} params={params} />
          </SItemContainer>
        ))}
      </>
    );
  },
);

interface ItemInputProps<T> {
  type: T;
  params: SharedValue<any>;
  itemsRef: RefObject<{[id: string]: UISelectorInput | null}>;
}

const ItemInput = memo((props: ItemInputProps<IInputFilterType>) => {
  const {type, ...rest} = props;
  if (!Array.isArray(type))
    return <_ItemInput {...(props as ItemInputProps<_TInputFilter>)} />;

  const visible = useSharedValue(0);
  const contentHeight = useSharedValue(0);
  const theme = useTheme();

  const aViewStyle = useAnimatedStyle(() => {
    return {
      height: interpolate(visible.value, [0, 1], [0, contentHeight.value]),
      opacity: visible.value,
    };
  }, []);

  const aIconStyle = useAnimatedStyle(
    () => ({
      transform: [
        {rotate: interpolate(visible.value, [0, 1], [270, 90]) + 'deg'},
      ],
    }),
    [],
  );

  return (
    <View>
      <SView />
      <SAdvancedSection
        onPress={() => {
          visible.value = withTiming(visible.value ? 0 : 1);
        }}>
        <SAdvancedTitle>{translate('policy.advanced_filter')}</SAdvancedTitle>
        <SAdvancedDropIcon style={aIconStyle} source={IC_ARROW} />
      </SAdvancedSection>
      <Animated.View
        style={[aViewStyle, {overflow: 'scroll', justifyContent: 'flex-end'}]}>
        <View
          onLayout={e => {
            contentHeight.value = e.nativeEvent.layout.height;
          }}>
          {type.map(item => (
            <_ItemInput type={item} {...rest} />
          ))}
        </View>
      </Animated.View>
    </View>
  );
});

const SMyKeyboardAvoidingView = styled(MyKeyboardAvoidingView)`
  background-color: ${p => p.theme.white};
  margin-top: 8px;
`;

const SView = styled.View`
  height: 8px;
  background-color: ${p => p.theme.neutral8};
  width: 140%;
  align-self: center;
`;

const SAdvancedSection = styled.TouchableOpacity`
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
`;

const SAdvancedTitle = styled.Text`
  font-size: 16px;
  line-height: 24px;
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Medium};
`;

const SAdvancedDropIcon = styled(Animated.Image)`
  height: 17px;
  width: 17px;
`;

const _ItemInput = memo(
  ({type, params, itemsRef}: ItemInputProps<_TInputFilter>) => {
    if (typeof type !== 'string') {
      return type(params);
    }

    const title = useMemo(() => {
      if (type == 'receiver') return ['recipient'];
      if (type == 'doctype') return ['document_type'];
      if (type.indexOf('date') !== -1) return ['from_date', 'to_date'];
      return [type];
    }, [type]);

    /** --------------------------------------------------
     * Date input
     */
    if (type.indexOf('date') !== -1) {
      return (
        <>
          <SDateTitle>{translate('policy.' + type)}</SDateTitle>
          <SRow>
            <UIInput.DateTime
              style={{flex: 1}}
              mode={'date'}
              title={translate('policy.' + title[0])}
              params={params}
              keyName={type + '_from'}
            />
            <UIInput.DateTime
              style={{flex: 1, marginLeft: 16}}
              mode={'date'}
              title={translate('policy.' + title[1])}
              params={params}
              keyName={type + '_to'}
            />
          </SRow>
        </>
      );
    }

    /** ---------------------------------------------
     *  User Input
     */
    if (type == 'created_by') {
      return (
        <UIInput.UserPicker
          title={translate('policy.' + title[0])}
          params={params}
          keyName={type}
          type={'user'}
        />
      );
    }

    /** --------------------------------------------------
     * Selector Input
     */
    const storageOpts = useStoragesByQuery('all').map(id => ({
      label: getStorages(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const collectionOpts = useCollectionsByQuery('all').map(id => ({
      label: getCollections(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const receiverOpts = useUserGroupByQuery('teams').map(id => ({
      label: getUserGroup(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    const doctypeOpts = useDoctypesByQuery('all').map(id => ({
      label: getDoctypes(id)?.name || translate('policy.undefined'),
      value: id,
    }));

    /* set folder options when collection change*/
    const [collection, setCollection] = useState('');

    if (type == 'folder') {
      useAnimatedReaction(
        () => params.value?.collection,
        (_collection, _prevCollection) => {
          if (_collection !== _prevCollection) {
            runOnJS(setCollection)(_collection);
          }
        },
        [],
      );
    }

    const folderOpts =
      useCollections(collection)?.folders?.map(item => ({
        label: item?.name || translate('policy.undefined'),
        value: item?.id,
      })) || [];

    const options = useMemo(() => {
      if (type == 'collection') return collectionOpts;
      if (type == 'storage') return storageOpts;
      if (type == 'receiver') return receiverOpts;
      if (type == 'doctype') return doctypeOpts;
      if (type == 'folder') return folderOpts;
      if (type == 'sort') return sortOpts;
      if (type == 'filter') return filterOpts;
      return [];
    }, [type, collection]) as any;

    const allOption = useMemo(() => {
      const value = undefined;

      if (type == 'filter')
        return {label: translate('policy.no_filter'), value};
      if (type == 'sort')
        return {label: translate('policy.created_time'), value};
      return {label: translate('policy.all'), value};
    }, [type]);

    return (
      <UIInput.Selector
        ref={view => {
          if (itemsRef.current) itemsRef.current[type] = view;
        }}
        title={translate('policy.' + title[0])}
        params={params}
        keyName={type}
        // value={""}
        options={[allOption, ...options]}
        hideClearButton={true}
      />
    );
  },
);

const SItemContainer = styled.View`
  margin: 16px 16px 0 16px;
`;

const SRow = styled.View`
  flex-direction: row;
`;
const SDateTitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Regular};
  margin-bottom: 4px;
  margin-top: 12px;
  color: ${p => p.theme.neutral1};
`;
