import React, {memo} from 'react';
import {DynamicHeader, ScreenWrapper, UITextInput} from '@true/ui';
import {styled, translate, useNavigationParams} from '@true/core';
import {
  defaultHomeFilterParams,
  IHomeFilterParams,
} from '@policy/screens/HomeScreen/index';
import {FilterComp} from '@policy/components/FilterComp';

export interface HomeFilterProps {
  params: IHomeFilterParams;
  setParams: (value: any) => void;
}

export const HomeFilter = memo(() => {
  const {params, setParams} = useNavigationParams<HomeFilterProps>();

  return (
    <SScreenWrapper>
      <DynamicHeader
        isAnimatedDuration={true}
        title={translate('policy.filter')}
      />
      <FilterComp
        items={[
          'collection',
          'folder',
          'storage',
          'receiver',
          'doctype',
          'filter',
          'sort',
          [
            params => (
              <>
                <UITextInput
                  title={translate('policy.total_documents')}
                  keyName={'limit'}
                  params={params}
                  keyboardType={'numeric'}
                />
                <SText>{translate('policy.number_of_document...')}</SText>
              </>
            ),
            'created_date',
            'effective_date',
            'expired_date',
          ],
        ]}
        params={params}
        setParams={setParams}
        defaultParams={defaultHomeFilterParams}
      />
    </SScreenWrapper>
  );
});

const SScreenWrapper = styled(ScreenWrapper)`
  background-color: ${p => p.theme.neutral8};
`;

const SText = styled.Text`
  color: ${p => p.theme.neutral2};
  font-size: 12px;
  line-height: 16px;
  margin: 4px 0;
`;
